<div><v-layout><v-app-bar id="color" absolute style="overflow: visible"><v-app-bar-nav-icon class="navbarcolor" id="btnnavbar" @click="clickdrawer()"></v-app-bar-nav-icon><v-app-bar-title><a>Mon parcours</a></v-app-bar-title></v-app-bar><v-navigation-drawer class="overflow-" id="color" v-model="drawer" absolute temporary><v-list nav dense><div v-for="item in redirectitems"><v-list-item :prepend-icon="item.icon" :title="item.title" @click="redirectnavbar(item.path)"></v-list-item></div></v-list></v-navigation-drawer><v-main><v-card class="mx-auto overflow-hidden" height="100vh" width="0vw"></v-card></v-main><div id="main"><v-card id="ecartcategorie" elevation="0"></v-card><v-card id="ecartcategorie" elevation="0"></v-card><div style="width: 90vw;"><v-card width="80%"><v-card-title><a>2018 | Initiation au développement</a><v-btn @click="redirect('https://www.nuitducode.net/presentation')" style="margin-left: 8vw;"><a>En savoir +</a></v-btn></v-card-title><v-card-text><div><b><u>A3Multimedia | Cholet | 17 Décembre 2018 - 21 Décembre 2018</u></b></div><p>Ce stage "d'observation" réalisé en 3ème m'a permis de confirmer ma voie professionnelle et de m'initier au développement.</p><p>J'ai notamment suivi un développeur web, étudié un site web créé par l'entreprise (recherche de bugs, failles, etc.), et<a> me suis initié au logiciel de développement WinDev, en relation avec une base de données.</a></p><p>Une première expérience pratique qui a définitivement confirmé ma vocation !</p><br></v-card-text></v-card><v-card width="80%"><v-card-title><a>Bac général spécialité Numérique et Science de l'Informatique (NSI)</a></v-card-title><v-card-text><div><b><u>Lycée Alain | Alençon | Septembre 2019 - Juillet 2022</u></b></div><p>Cursus scolaire (2nde à terminale).</p><p>Option science de l'ingénieur, spécialité NSI (Algorithmique, Python, SQL), complémentaires mathématiques et litérature anglaise.</p><br></v-card-text></v-card><v-card width="80%"><v-card-title><a>Concours d'informatique</a><v-btn @click="redirect('https://www.nuitducode.net/presentation')" style="margin-left: 14vw;"><a>En savoir +</a></v-btn></v-card-title><v-card-text><div><b><u>Lycée Alain | Alençon | 9 Juin 2022</u></b></div><p>Participation à la nuit du c0de, concours international de développement en groupe avec la classe de NSI.</p><br></v-card-text></v-card><v-card width="80%"><v-card-title><a>Immersion de 2 mois en anglais</a><v-btn @click="redirect('https://www.maltadvice.com/fr/ef-malte/')" style="margin-left: 9.5vw;"><a>Voir le site</a></v-btn></v-card-title><v-card-text><div><b><u>EF Formation | St. Julian's | Malte | 10 Juillet 2022 - 25 Aout 2022</u></b></div><p>2 mois d'immersion à Malte avec l'école EF Formation.</p><p>Programme de cours intensif de 224h.</p><p>Echange culturel linguistique en environnement international.</p><br></v-card-text></v-card></div></div></v-layout></div>